@import url(https://fonts.googleapis.com/css2?family=Metal+Mania&display=swap);
body{height:100vh}.button{font-family:"Metal Mania", cursive;background-color:burlywood;border:none;padding:10px 20px;border-radius:20px;box-shadow:rgba(100,100,111,0.2) 0 7px 29px 0;width:100px}.button:hover{background-color:#bdb5ab;border:3px solid #5f5f5f;padding:7px 17px}.button:focus{outline:none}.buttons{display:flex;grid-gap:20px;gap:20px}.allClock{font-family:"Metal Mania", cursive;display:flex;flex-direction:column;align-items:center;justify-content:center}.clock{text-align:center}.clock__name{color:#860101}

